"use strict";

const SettingsModule = (function () {
    let settings = {
            'secondary-title': {
                status: true,
                selector: '.secondary-title',
                type: 'checkbox'
            },
            'title': {
                status: true,
                selector: '.title',
                type: 'checkbox'
            },
            'subtitle': {
                status: true,
                selector: '.author',
                type: 'checkbox'
            },
            'secondary-btn': {
                status: true,
                selector: '.learn-more',
                type: 'checkbox'
            },
            'theme': {
                val: 'theme-white',
                selector: '#theme-container',
                type: 'radio'
            }
        },
        tabSections = document.querySelectorAll('.section'),
        addClassToAll = (selectors, className) => {
            selectors.forEach((el) => {
                el.classList.add(className);
            })
        },
        removeClassToAll = (selectors, className) => {
            selectors.forEach((el) => {
                el.classList.remove(className);
            })
        },
        setCheckboxStatus = (status, selector) => {
            if (document.querySelector(selector)){
                const list = document.querySelectorAll(selector);
                if (status) {
                    removeClassToAll(list, 'hide');
                } else {
                    addClassToAll(list, 'hide');
                }
            }
        },
        setRadioStatus = (theme, selector) => {
            if (document.querySelector(selector)) {
                document.querySelector(selector).classList = theme;
            }
        },
        hideScroll = () => {
            document.body.style.overflow = 'hidden';
        },
        showScroll = () => {
            document.body.style.overflow = 'auto';
        },
        saveSettings = (cb) => {
            for ( let key in settings) {
                const {type, selector} = settings[key];
                if (type === 'checkbox') {
                    setCheckboxStatus(settings[key].status, selector);
                } else if (type === 'radio') {
                    setRadioStatus(settings[key].val, selector);
                }

            }
            cb();
        },
        closeModal = () => {
            document.querySelector('.modal').classList = 'hide';
            showScroll();
        },
        onClickTab = () => {
            const tabList = document.querySelectorAll('.tab-btn');
            tabList.forEach( el => {
                el.addEventListener('click', function () {
                    const sectionName = this.getAttribute('data-section');
                    removeClassToAll(tabList, 'active');
                    removeClassToAll(tabSections, 'active');
                    this.classList.add('active');
                   document.querySelector(`form[name=${sectionName}]`).classList.add('active');
                });
            })
        },
        events = () => {
            document.querySelectorAll('.general-control').forEach((control) => {
                control.addEventListener('change', function() {
                    const {name, checked} = this;
                    settings[name].status = checked;
                })
            });

            document.appearance.theme.forEach((control) => {
                control.addEventListener('change', function() {
                    const {name, value} = this;
                    settings[name].val = value;
                })
            });

            document.querySelector('#cancel-btn').addEventListener('click', () => {
                closeModal();
            });

            document.querySelector('#save-btn').addEventListener('click', () => {
                saveSettings(() => {
                    closeModal();
                });
            });

            onClickTab();
        }
    return {
        init: () => {
            hideScroll();
            events();
        }
    }
})();

SettingsModule.init();



