tBackground: #FFFFFF,\n                footerBackground: #ffffff,\n                authorColor: #8E959E,\n                titleColor: #3625A9,\n                descriptionColor: #646C75,\n                learnMoreColor: #3625A9,\n                learnMoreColorBnt: #10161C,\n                footerTextColor: #646C75,\n                copyrightColor: #8E959E,\n                logoColor: #3625A9,\n                menuColor: #10161C,\n                phoneColor: #10161C,\n        ),\n        gray: (\n                headerBackgroundColor:#ffffff,\n                btnBackground: #ffffff,\n                btnColor: #ffffff,\n                backgroundColor: #FAFAFA,\n                seconderyTitlecolor: #8E959E,\n                articlesColor: #181E29,\n                paragrafColor: #646C75,\n                postBackground: #FFFFFF,\n                footerBackground: #FAFAFA,\n                authorColor: #646C75,\n                titleColor: #3625A9,\n                descriptionColor: #646C75,\n                learnMoreColor: #3625A9,\n                learnMoreColorBnt: #10161C,\n                footerTextColor: #181E29,\n                copyrightColor: #646C75,\n                logoColor: #3625A9,\n                menuColor: #10161C,\n                phoneColor: #10161C,\n        ),\n        dark: (\n                headerBackgroundColor:#10161C,\n                btnBackground: #ffffff,\n                btnColor: #ffffff,\n                backgroundColor: #10161C,\n                seconderyTitlecolor: #FFFFFF,\n                articlesColor: #FFFFFF,\n                paragrafColor: #FFFFFF,\n                postBackground: #181E29,\n                footerBackground: #10161C,\n                authorColor: #B0B8BF,\n                titleColor: #6555D0,\n                descriptionColor: #DADDE0,\n                learnMoreColor: #6555D0,\n                learnMoreColorBnt: #FFFFFF,\n                footerTextColor: #8E959E,\n                copyrightColor: #8E959E,\n                logoColor: #ffffff,\n                menuColor: #FFFFFF,\n                phoneColor: #FFFFFF,\n        ),\n);\n\n/*\n * Implementation of themes\n */\n@mixin themify($themes) {\n  @each $theme, $map in $themes {\n    .theme-#{$theme} & {\n      $theme-map: () !global;\n      @each $key, $submap in $map {\n        $value: map-get(map-get($themes, $theme), '#{$key}');\n        $theme-map: map-merge($theme-map, ($key: $value)) !global;\n      }\n      @content;\n      $theme-map: null !global;\n    }\n  }\n}\n\n@function themed($key) {\n  @return map-get($theme-map, $key);\n}\n\n\nbody {\n  margin: 0;\n  overflow-x: hidden;\n}\n\nhtml, body {\n  height: 100%;\n}\n.hide{\n  display: none !important;\n}\n.modal{\n  position: fixed;\n  display: flex;\n  width: 100vw;\n  height: 100vh;\n  background-color: #ffffff;\n  z-index: 9;\n  justify-content: center;\n  align-items: center;\n  .setting-container{\n    border-radius: 5px;\n    width: 100%;\n    max-width: 227px;\n    border: 1px solid #B0B8BF;\n    .section{\n      display: none;\n      margin-top: 20px;\n      flex-direction: row;\n      flex-wrap: wrap;\n      div{\n        width: 100%;\n      }\n      &.appearance{\n        justify-content: space-around;\n      }\n      .theme-radio{\n        display: block;\n        position: relative;\n        margin-bottom: 12px;\n        cursor: pointer;\n        font-size: 22px;\n        -webkit-user-select: none;\n        -moz-user-select: none;\n        -ms-user-select: none;\n        user-select: none;\n        width: 20px;\n        height: 20px;\n        input{\n          position: absolute;\n          opacity: 0;\n          cursor: pointer;\n          left: 0;\n          top: 0;\n          z-index: 9;\n          width: 100%;\n          height: 100%;\n          &:checked ~ .checkmark{\n            &:after{\n              display: block;\n            }\n          }\n        }\n        .checkmark {\n          position: absolute;\n          top: 0;\n          left: 0;\n          height: 25px;\n          width: 25px;\n          background-color: #eee;\n          border-radius: 5px;\n          border: 1px solid #8E959E;\n          &.white{\n            background-color: #ffffff;\n          }\n          &.dark{\n            background-color: #10161C;\n          }\n          &:after{\n            content: '';\n            top: 0px;\n            width: 8px;\n            height: 8px;\n            border-radius: 50%;\n            background: #000000;\n            z-index: 2;\n            margin-left: auto;\n            margin-right: -3px;\n            margin-top: -3px;\n          }\n        }\n      }\n      &.active{\n        display: flex !important;\n      }\n    }\n    .setting-title{\n      background: #B0B8BF;\n      padding: 10px 30px;\n      margin-bottom: 20px;\n      color: #10161C;\n      font-weight: 600;\n      font-family: 'Source Sans Pro', sans-serif;\n    }\n    .settings-text{\n      padding: 0 30px;\n      font-family: 'Source Sans Pro', sans-serif;\n      color: #10161C;\n      font-weight: 600;\n    }\n    form{\n      padding: 0 30px;\n      font-family: 'Source Sans Pro', sans-serif;\n      color: #10161C;\n      font-weight: 600;\n    }\n    .btn-cont{\n      display: flex;\n      margin-top: 30px;\n      button{\n        width: 50%;\n        height: 35px;\n        background: transparent;\n        border-top: 1px solid #B0B8BF;\n        border-bottom: none;\n        outline: none !important;\n        font-family: 'Source Sans Pro', sans-serif;\n        color: #10161C;\n        font-weight: 600;\n        font-size: 15px;\n        &:first-child{\n          border-left: none;\n          border-right: none;\n        }\n        &:last-child{\n          border-right: none;\n        }\n      }\n    }\n    .tabs{\n      display: flex;\n      justify-content: center;\n      .tab-btn{\n        width: 100px;\n        background: transparent;\n        border: 1px solid gray;\n        height: 35px;\n        color:  #181E29;\n        outline: none !important;\n        cursor: pointer;\n        &:first-child{\n          border-top-left-radius: 5px;\n          border-bottom-left-radius: 5px;\n          border-right: none;\n        }\n        &:last-child{\n          border-top-right-radius: 5px;\n          border-bottom-right-radius: 5px;\n          border-left: none;\n        }\n        &.active{\n          background:  #181E29;\n          color: #ffffff;\n        }\n      }\n    }\n  }\n}\n/*header styles start*/\n\n.header{\n  display: flex;\n  justify-content: center;\n  flex-wrap: wrap;\n  height: 80px;\n  @include themify($themes) {\n    background-color: themed('headerBackgroundColor');\n  }\n  .logo{\n    max-width: 88px;\n    display: flex;\n    align-items: center;\n    margin-left: 18px;\n    img{\n      margin-right: 7px;\n    }\n    span{\n      font-family: 'IBM Plex Serif', serif;\n      font-style: normal;\n      font-weight: 500;\n      font-size: 18px;\n      line-height: 24px;\n      display: flex;\n      align-items: center;\n      @include themify($themes) {\n        color: themed('logoColor');\n      }\n    }\n  }\n  nav{\n    margin-left: auto;\n    margin-right: 92px;\n    ul{\n      list-style-type: none;\n      display: flex;\n      font-family: 'Source Sans Pro', sans-serif;\n      font-style: normal;\n      font-weight: 600;\n      font-size: 16px;\n      line-height: 28px;\n      align-items: center;\n      margin: 0;\n      height: 100%;\n      li{\n        @include themify($themes) {\n          color: themed('menuColor');\n        }\n        &:last-child{\n          margin-left: 45px;\n        }\n      }\n    }\n  }\n  .phone{\n    font-family: 'Open Sans', sans-serif;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 16px;\n    line-height: 28px;\n    display: flex;\n    align-items: center;\n    @include themify($themes) {\n      color: themed('phoneColor');\n    }\n  }\n}\n/*header styles and*/\n\n/*main content styles start*/\nsecti